<template>
    <div id="micuenta">
        <div class="contenedor">
            <h2>Editar perfil</h2>
            <form action="" class="cuenta-form">
                <div class="contenedor-img-perfil">
                    <img 
                        src="../assets/user.svg" 
                        class="img-perfil" 
                        style="padding:10px" 
                        v-if="$store.state.usuarioActivo.imagenPerfil == null"
                    >
                    <img 
                        :src="$store.state.usuarioActivo.imagenPerfil" 
                        class="img-perfil" 
                        v-if="$store.state.usuarioActivo.imagenPerfil != null"
                    >
                    <p>Cambiar imagen</p>
                </div>
                <div class="separador"><p>Datos de la cuenta</p></div>
                <div class="row">
                    <div class="col-md-6">
                        <input type="text" v-model="nombre" placeholder="Nombre" required>
                        <input type="text" v-model="apellido" placeholder="Apellido" required>
                        <input type="text" v-model="dni" placeholder="Dni/Pasaporte" required>
                    </div>
                    <div class="col-md-6">
                        <input type="text" v-model="fechaNacimiento" placeholder="Fecha de nacimiento" required>
                        <input type="text" v-model="telefono" placeholder="Telefono" required>
                    </div>
                </div>
                <div class="separador"><p>Cambio de contraseña</p></div>
                <div class="row">
                    <div class="col-md-6">
                        <input type="password" v-model="contraseña" placeholder="Nueva contraseña" required>
                        <input type="password" v-model="contraseñaConfirm" placeholder="Confirmar contraseña" required>
                    </div>
                    <div class="col-md-6">
                        <input type="password" v-model="contraseñaActual" placeholder="Contraseña actual" required>
                    </div>
                </div>
                <button type="submit">Guardar cambios</button>
            </form>
        </div>
    </div>
</template>

<style lang="scss">
    #micuenta {
        min-height: calc(100vh - 90px - 210px);
        padding: 80px 0;
        .contenedor {
            margin: 0 auto;
            width: 100%;
            max-width: 1100px;
            background: white;
            border-radius: 6px;
            box-shadow: 1px 1px 6px 0 #ccc;
            padding: 30px;
            text-align: center;
            .contenedor-img-perfil {
                position: relative;
                width: 120px;
                margin: 40px auto 0 auto;
                cursor:pointer;
                .img-perfil {
                    height: 120px;
                    width: 120px;
                    border-radius: 20%;
                    background: #de7f71;
                }
                p {
                    position: absolute;
                    margin: 0;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 20%;
                    background: rgba(230,230,230,0.7);
                    visibility: hidden;
                    opacity: 0;
                    transition: all 0.5s;
                }
            }
            .contenedor-img-perfil:hover {
                p {
                    visibility: visible;
                    opacity: 1;
                }
            }
            .separador {
                position: relative;
                width: 100%;
                margin: 40px 0;
                display: flex;
                justify-content: center;
                p {
                    margin:0;
                    padding: 0 15px;
                    background: white;
                    z-index: 9999;
                    color: #777;
                }
            }
            .separador::after {
                content: '';
                height: 1px;
                width: 100%;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                background: #aaa;
            }
            .cuenta-form {
                text-align: center;
                width: 100%;
                input{
                    width: 100%;
                    padding: 8px 10px;
                    border: 1px solid #ccc;
                    border-radius: 6px;
                    outline: none;
                    transition: all 0.4s ease-in-out;
                    margin-bottom: 30px;
                }
                input:focus {
                    border: 1px solid #de7f71;
                }
                button{
                    display: block;
                    margin: 30px auto 30px auto;
                    padding: 8px;
                    width: 100%;
                    max-width: 350px;
                    outline: none;
                    border-radius: 6px;
                    background-color: #de7f71;
                    color: white;
                    transition: all 0.4s ease-in-out;
                    border: none;
                }
                button:hover {
                    background-color: #ee8878;
                    color: white;
                }
            }
        }
    }
</style>

<script>
export default {
    name: 'MiCuenta',
    data() {
        return {
            nombre: '',
            apellido: '',
            dni: '',
            fechaNacimiento: '',
            telefono: '',
            email: '',
            contraseña: '',
            contraseñaConfirm: '',
            contraseñaActual: ''
        }
    },
}
</script>